body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.p-card-blog__block {
  background-color: #D2D2D2;
  border-radius: 10px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.25);
}

.p-card-blog__block:nth-child(n+2) {
  margin-top: 1.25rem;
}
@media screen and (min-width: 768px) {
  .p-card-blog__block:nth-child(n+2) {
    margin-top: initial;
  }
}

.p-card-blog__body {
  overflow: hidden;
  position: relative;
  padding: 1.25rem 1.25rem 1.4375rem 1.25rem;
}
@media screen and (min-width: 768px) {
  .p-card-blog__body {
    padding: 1.25rem 1.25rem 1.375rem 1.25rem;
  }
}

.p-card-blog__block:hover {
  background-color: #3D593D;
}

.p-card-blog__block:hover .c-card-blog__title {
  color: #F7F7F7;
}

.p-card-blog__block:hover .c-card-blog__text {
  color: #F7F7F7;
}

.p-card-blog__block:hover::after {
  transform: scale(1, 1);
}

@media screen and (min-width: 768px) {
  .p-card-blog__block:nth-child(n+2) {
    margin-left: 3.5rem;
  }
}

.p-card-blog__text {
  margin-top: 0.45625rem;
}
@media screen and (min-width: 768px) {
  .p-card-blog__text {
    margin-top: 0.45625rem;
  }
}